.App {
  text-align: left;
}

.App-header {
  background-color: #A1887F;
  height: 70px;
  padding-left: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
}

.App-title {
  font-size: 1.9em;
  font-family: Roboto;
}

.Block-title {
  font-size: 0.7em;
  font-family: "Roboto Thin";
}

.Block-result {
  font-size: 0.6em;
  font-family: Roboto;
  white-space: pre-line;
}

.Prog-form {
  font-size: 0.9em;
  font-family: "Roboto Light";
}

.App-intro {
  font-size: large;
}

.wrapper {
  display: grid;
  grid-gap: 2px;
  grid-template-columns: [col1-start] 35%  [col2-start] 35%  [col3-start] 30% [col3-end];
  grid-template-rows: [row1-start] 100%;
  width: 100vw;
  height: 100vh;
}

.box {
  border-radius: 1px;
  padding: 40px;
  font-size: 150%;
}

.b {
  grid-column: col3-start ;
  grid-row: row1-start / row1-end;
  background-color: #EFEBE9;
}
.c {
  color: #90A4AE;
  grid-column: col1-start;
  grid-row: row1-start ;
  background-color: #fff;
}
.d {
  color: #90A4AE;
  grid-column: col2-start ;
  grid-row: row1-start ;
  background-color: #fff;
}

select {
  border: 1px solid #111;
  background: transparent;
  width: 230px;
  padding: 5px 35px 5px 5px;
  font-size: 20px;
  font-family: "Roboto Thin";
  border: 1px solid #ccc;
  height: 50px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: url("logo.jpg") 96% / 15% no-repeat #eee;
}

.Run-Button {
  border: 1px solid #111;
  background: transparent;
  width: 230px;
  padding: 5px 35px 5px 5px;
  font-size: 20px;
  font-family: "Roboto Light";
  border: 1px solid #ccc;
  height: 50px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}



